.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(2px);
  animation: fadeIn 0.2s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.modalContent {
  background: white;
  border-radius: 12px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  max-width: 800px;
  width: 90%;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
  animation: slideUp 0.3s ease-out;
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.modalHeader {
  padding: 20px 24px;
  border-bottom: 1px solid #e5e7eb;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modalTitle {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
  color: #1f2937;
}

.headerActions {
  display: flex;
  gap: 8px;
  align-items: center;
}

.copyButton {
  padding: 6px 12px;
  background: #f3f4f6;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  color: #374151;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  gap: 4px;
}

.copyButton:hover {
  background: #e5e7eb;
  border-color: #9ca3af;
}

.copyButton.copied {
  background: #10b981;
  color: white;
  border-color: #10b981;
}

.closeButton {
  width: 32px;
  height: 32px;
  border-radius: 6px;
  border: none;
  background: transparent;
  color: #6b7280;
  font-size: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}

.closeButton:hover {
  background: #f3f4f6;
  color: #1f2937;
}

.modalBody {
  flex: 1;
  overflow: auto;
  padding: 24px;
  background: #f9fafb;
}

.jsonContent {
  margin: 0;
  padding: 16px;
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  font-family: 'SF Mono', 'Monaco', 'Inconsolata', 'Fira Code', monospace;
  font-size: 13px;
  line-height: 1.6;
  color: #1f2937;
  overflow-x: auto;
  white-space: pre;
  tab-size: 2;
}

/* Scrollbar styling */
.modalBody::-webkit-scrollbar,
.jsonContent::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.modalBody::-webkit-scrollbar-track,
.jsonContent::-webkit-scrollbar-track {
  background: #f3f4f6;
  border-radius: 4px;
}

.modalBody::-webkit-scrollbar-thumb,
.jsonContent::-webkit-scrollbar-thumb {
  background: #d1d5db;
  border-radius: 4px;
}

.modalBody::-webkit-scrollbar-thumb:hover,
.jsonContent::-webkit-scrollbar-thumb:hover {
  background: #9ca3af;
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
  .modalContent {
    background: #1f2937;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  }

  .modalHeader {
    border-bottom-color: #374151;
  }

  .modalTitle {
    color: #f3f4f6;
  }

  .copyButton {
    background: #374151;
    border-color: #4b5563;
    color: #e5e7eb;
  }

  .copyButton:hover {
    background: #4b5563;
    border-color: #6b7280;
  }

  .closeButton {
    color: #9ca3af;
  }

  .closeButton:hover {
    background: #374151;
    color: #f3f4f6;
  }

  .modalBody {
    background: #111827;
  }

  .jsonContent {
    background: #1f2937;
    border-color: #374151;
    color: #f3f4f6;
  }

  .modalBody::-webkit-scrollbar-track,
  .jsonContent::-webkit-scrollbar-track {
    background: #374151;
  }

  .modalBody::-webkit-scrollbar-thumb,
  .jsonContent::-webkit-scrollbar-thumb {
    background: #4b5563;
  }

  .modalBody::-webkit-scrollbar-thumb:hover,
  .jsonContent::-webkit-scrollbar-thumb:hover {
    background: #6b7280;
  }
}