/* iPhone-style speech bubble with typing animation */

.speechBubbleContainer {
  display: flex;
  justify-content: flex-start;
  margin: 1rem 0;
  padding: 0 1rem;
}

.speechBubble {
  background: #e5e5ea;
  border-radius: 18px;
  padding: 12px 16px;
  position: relative;
  max-width: 80px;
  min-height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fadeIn 0.3s ease-in-out;
}

/* Speech bubble tail */
.speechBubble::before {
  content: '';
  position: absolute;
  left: -7px;
  bottom: 4px;
  width: 0;
  height: 0;
  border: 7px solid transparent;
  border-right-color: #e5e5ea;
  border-left: 0;
  border-bottom: 0;
  transform: rotate(-10deg);
}

.typingDots {
  display: flex;
  gap: 4px;
  align-items: center;
}

.dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #999;
  animation: typingAnimation 1.4s infinite ease-in-out;
}

.dot:nth-child(1) {
  animation-delay: 0s;
}

.dot:nth-child(2) {
  animation-delay: 0.2s;
}

.dot:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes typingAnimation {
  0%, 60%, 100% {
    transform: translateY(0);
    opacity: 0.4;
  }
  30% {
    transform: translateY(-10px);
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
  .speechBubble {
    background: #3a3a3c;
  }
  
  .speechBubble::before {
    border-right-color: #3a3a3c;
  }
  
  .dot {
    background: #8e8e93;
  }
}