/* Import from NodeDetailsPanel styles - shared base styles */

/* Markdown content styling */
.markdownContent {
  font-size: 0.875rem; /* 14px */
  line-height: 1.6;
  color: #374151;
}

.markdownContent h1,
.markdownContent h2,
.markdownContent h3,
.markdownContent h4,
.markdownContent h5,
.markdownContent h6 {
  margin-top: 1rem; /* Reduced from 1.5rem */
  margin-bottom: 0.5rem; /* Reduced from 0.75rem */
  font-weight: 600;
  line-height: 1.4;
}

.markdownContent h1 { font-size: 1.125rem; } /* Reduced from 1.25rem */
.markdownContent h2 { font-size: 1rem; } /* Reduced from 1.125rem */
.markdownContent h3 { font-size: 0.9375rem; } /* Reduced from 1rem */
.markdownContent h4 { font-size: 0.875rem; }

.markdownContent p {
  margin-bottom: 0.75rem; /* Reduced from 1rem */
  font-size: 0.875rem; /* Ensure consistent sizing */
}

.markdownContent ul,
.markdownContent ol {
  padding-left: 1.25rem;
  margin-bottom: 1rem;
}

.markdownContent li {
  margin-bottom: 0.25rem;
}

.markdownContent code {
  background: #f3f4f6;
  padding: 0.125rem 0.25rem;
  border-radius: 0.25rem;
  font-size: 0.8125rem;
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
}

.markdownContent pre {
  background: #f3f4f6;
  padding: 0.75rem;
  border-radius: 0.5rem;
  overflow-x: auto;
  margin-bottom: 1rem;
}

.markdownContent pre code {
  background: none;
  padding: 0;
}

.markdownContent strong {
  font-weight: 600;
}

.markdownContent em {
  font-style: italic;
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.3);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
  z-index: 999;
}

.overlayVisible {
  opacity: 1;
  visibility: visible;
}

.panel {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  width: 400px;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  box-shadow: -2px 0 8px rgba(0, 0, 0, 0.1);
  transform: translateX(100%);
  transition: transform 0.3s ease;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.panelOpen {
  transform: translateX(0);
}

.panelHeader {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
  border-bottom: 1px solid #e5e7eb;
  background: rgba(255, 255, 255, 0.8);
}

.headerContent {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.headerActions {
  display: flex;
  align-items: center;
  gap: 8px;
}

.actionButton {
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
  padding: 6px;
  border-radius: 6px;
  transition: all 0.2s ease;
  opacity: 0.7;
}

.actionButton:hover {
  background: rgba(0, 0, 0, 0.1);
  opacity: 1;
}

.headerMeta {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.edgeIdHeader {
  font-size: 12px;
  color: #6b7280;
  font-weight: 500;
}

.edgeIdHeader code {
  font-family: monospace;
  background: #f3f4f6;
  padding: 2px 6px;
  border-radius: 4px;
  font-size: 11px;
  margin-left: 4px;
}

.panelHeader h3 {
  margin: 0;
  font-size: 20px;
  color: #111827;
  font-weight: 600;
}

.typeTag {
  display: inline-block;
  padding: 4px 12px;
  background: #f3e8ff;
  color: #7c3aed;
  border-radius: 12px;
  font-size: 14px;
  font-weight: 500;
}

.closeButton {
  background: none;
  border: none;
  font-size: 24px;
  color: #6b7280;
  cursor: pointer;
  padding: 4px 8px;
  line-height: 1;
  transition: color 0.2s ease;
}

.closeButton:hover {
  color: #111827;
}

.panelContent {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}

.section {
  margin-bottom: 24px;
}

.section:last-child {
  margin-bottom: 0;
}

.section label {
  display: block;
  font-size: 12px;
  font-weight: 600;
  color: #6b7280;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-bottom: 8px;
}

.section p {
  margin: 0;
  color: #374151;
  line-height: 1.6;
}

.edgeId {
  display: block;
  padding: 8px 12px;
  background: #f3f4f6;
  border-radius: 6px;
  font-family: monospace;
  font-size: 12px;
  color: #6b7280;
  word-break: break-all;
}

.connectionInfo {
  display: flex;
  flex-direction: column;
  gap: 8px;
  background: #f9fafb;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 16px;
}

.connectionItem {
  display: flex;
  align-items: center;
  gap: 8px;
}

.connectionLabel {
  font-size: 12px;
  font-weight: 600;
  color: #6b7280;
  min-width: 40px;
}

.nodeRef {
  display: inline-block;
  padding: 6px 10px;
  background: #e0f2fe;
  color: #0369a1;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: help;
  transition: background-color 0.2s ease;
  word-break: break-word;
}

.nodeRef:hover {
  background: #bae6fd;
}

.connectionArrow {
  font-size: 16px;
  color: #6b7280;
  text-align: center;
  padding: 4px 0;
}

.typeValue {
  color: #374151;
  font-weight: 500;
}

/* Timestamp styling */
.timestampInfo {
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-size: 14px;
  color: #374151;
  line-height: 1.5;
}

.timestampInfo > div {
  display: flex;
  align-items: center;
  gap: 8px;
}

.timestampInfo strong {
  font-weight: 600;
  color: #6b7280;
  min-width: 60px;
  font-size: 13px;
}

.rawDataPre {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 4px;
  padding: 12px;
  font-size: 11px;
  color: #495057;
  overflow-x: auto;
  max-height: 300px;
  overflow-y: auto;
  margin-top: 8px;
  line-height: 1.4;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .panel {
    width: 100%;
    max-width: 400px;
  }
}