/**
 * ToastContainer CSS - Beautiful Positioning
 * 
 * Uses CSS positioning instead of portals for simpler architecture.
 * Container positions toasts beautifully with proper stacking and animations.
 */

.toastContainer {
  position: fixed;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  padding: 1rem;
  pointer-events: none; /* Allow clicks to pass through container */
  max-height: calc(100vh - 2rem);
  overflow-y: auto;
  z-index: 9999;
}

/* Position variations */
.topright {
  top: 0;
  right: 0;
  align-items: flex-end;
}

.topcenter {
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  align-items: center;
}

.topleft {
  top: 0;
  left: 0;
  align-items: flex-start;
}

.bottomright {
  bottom: 0;
  right: 0;
  flex-direction: column-reverse;
  align-items: flex-end;
}

.bottomcenter {
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  flex-direction: column-reverse;
  align-items: center;
}

.bottomleft {
  bottom: 0;
  left: 0;
  flex-direction: column-reverse;
  align-items: flex-start;
}

/* Toast wrapper for beautiful animations */
.toastWrapper {
  pointer-events: auto; /* Re-enable clicks on individual toasts */
  animation: slideIn 0.3s cubic-bezier(0.16, 1, 0.3, 1);
  max-width: 420px;
  min-width: 320px;
}

/* Mobile responsive */
@media (max-width: 640px) {
  .toastContainer {
    left: 1rem;
    right: 1rem;
    padding: 0.5rem;
  }
  
  .topcenter,
  .bottomcenter {
    left: 1rem;
    right: 1rem;
    transform: none;
  }
  
  .toastWrapper {
    min-width: auto;
    max-width: none;
  }
}

/* Beautiful slide-in animation */
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-1rem) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* Slide-out animation (applied via JavaScript) */
.toastWrapper.dismissing {
  animation: slideOut 0.2s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

@keyframes slideOut {
  from {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
  to {
    opacity: 0;
    transform: translateY(-0.5rem) scale(0.95);
  }
}