/* StepByStepViewer Component Styles */

.container {
    background: var(--color-bg-secondary, #f8f9fa);
    border-radius: 6px;
    padding: 1rem;
    border: 1px solid var(--color-border-subtle, #e5e7eb);
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
    padding-bottom: 0.5rem;
    border-bottom: 1px solid var(--color-border-subtle, #e5e7eb);
}

.title {
    margin: 0;
    font-size: 1rem;
    font-weight: 600;
    color: var(--color-text-primary, #1a1a1a);
}

.progress {
    font-size: 0.875rem;
    color: var(--color-text-secondary, #6b7280);
    font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
}

.timeline {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.step {
    display: flex;
    align-items: flex-start;
    gap: 0.75rem;
    padding: 0.75rem;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.2s ease;
    background: var(--color-bg-primary, #ffffff);
    border: 1px solid transparent;
    position: relative;
}

.step:hover {
    background: var(--color-bg-hover, #f3f4f6);
    border-color: var(--color-border-hover, #d1d5db);
}

.step:focus {
    outline: none;
    border-color: var(--color-primary, #2563eb);
    box-shadow: 0 0 0 1px var(--color-primary, #2563eb);
}

.step.active {
    background: var(--color-primary-bg, #eff6ff);
    border-color: var(--color-primary, #2563eb);
    box-shadow: 0 0 0 1px var(--color-primary, #2563eb);
}

.step.completed {
    background: var(--color-success-bg, #f0fdf4);
    border-color: var(--color-success, #22c55e);
}

.step.error {
    background: var(--color-error-bg, rgba(239, 68, 68, 0.1));
    border-color: var(--color-error, #ef4444);
}

.stepIndicator {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    background: var(--color-bg-secondary, #f8f9fa);
    border: 2px solid var(--color-border-subtle, #e5e7eb);
    flex-shrink: 0;
    font-weight: 600;
    font-size: 0.875rem;
    color: var(--color-text-secondary, #6b7280);
    transition: all 0.2s ease;
}

.step.active .stepIndicator {
    background: var(--color-primary, #2563eb);
    border-color: var(--color-primary, #2563eb);
    color: white;
}

.step.completed .stepIndicator {
    background: var(--color-success, #22c55e);
    border-color: var(--color-success, #22c55e);
    color: white;
}

.step.error .stepIndicator {
    background: var(--color-error, #ef4444);
    border-color: var(--color-error, #ef4444);
    color: white;
}

.stepNumber,
.checkIcon,
.errorIcon {
    font-size: 0.875rem;
}

.checkIcon {
    font-size: 0.75rem;
}

.errorIcon {
    font-size: 1rem;
}

.stepContent {
    flex: 1;
    min-width: 0;
}

.stepTitle {
    font-weight: 600;
    font-size: 0.875rem;
    color: var(--color-text-primary, #1a1a1a);
    margin-bottom: 0.25rem;
}

.stepSummary {
    font-size: 0.75rem;
    color: var(--color-text-secondary, #6b7280);
    line-height: 1.4;
}

.phaseInfo {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 0.25rem;
}

.phaseTag {
    display: inline-block;
    padding: 2px 6px;
    border-radius: 4px;
    font-size: 0.625rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.phase1 {
    background: #dbeafe;
    color: #1e40af;
}

.phase2 {
    background: #fef3c7;
    color: #92400e;
}

.phase3 {
    background: #dcfce7;
    color: #166534;
}

.phaseName {
    font-size: 0.75rem;
    color: var(--color-text-secondary, #6b7280);
    font-weight: 500;
}

.elementInfo {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 0.75rem;
    margin-top: 0.25rem;
}

.elementType {
    background: var(--color-bg-secondary, #f8f9fa);
    color: var(--color-text-secondary, #6b7280);
    padding: 1px 4px;
    border-radius: 3px;
    font-size: 0.625rem;
    text-transform: lowercase;
}

.errorMessage {
    font-size: 0.75rem;
    color: var(--color-error, #ef4444);
    font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
    background: var(--color-error-bg, rgba(239, 68, 68, 0.1));
    padding: 0.5rem;
    border-radius: 4px;
    margin-top: 0.5rem;
    border: 1px solid var(--color-error-border, rgba(239, 68, 68, 0.2));
}

.connector {
    position: absolute;
    left: 1.75rem;
    top: 100%;
    width: 2px;
    height: 0.5rem;
    background: var(--color-border-subtle, #e5e7eb);
    z-index: 1;
}

.step.completed .connector {
    background: var(--color-success, #22c55e);
}

.step.active .connector {
    background: var(--color-primary, #2563eb);
}

.empty {
    text-align: center;
    color: var(--color-text-secondary, #6b7280);
    font-style: italic;
    padding: 2rem;
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
    .container {
        --color-bg-primary: #1a1a1a;
        --color-bg-secondary: #2d2d2d;
        --color-bg-hover: #374151;
        --color-text-primary: #ffffff;
        --color-text-secondary: #cccccc;
        --color-border-subtle: #404040;
        --color-border-hover: #525252;
        --color-primary: #3b82f6;
        --color-primary-bg: #1e3a8a;
        --color-success: #22c55e;
        --color-success-bg: #064e3b;
        --color-error: #ef4444;
        --color-error-bg: rgba(239, 68, 68, 0.15);
        --color-error-border: rgba(239, 68, 68, 0.3);
    }
}

/* Light mode fallback */
.container {
    --color-bg-primary: #ffffff;
    --color-bg-secondary: #f8f9fa;
    --color-bg-hover: #f3f4f6;
    --color-text-primary: #1a1a1a;
    --color-text-secondary: #6b7280;
    --color-border-subtle: #e5e7eb;
    --color-border-hover: #d1d5db;
    --color-primary: #2563eb;
    --color-primary-bg: #eff6ff;
    --color-success: #22c55e;
    --color-success-bg: #f0fdf4;
    --color-error: #ef4444;
    --color-error-bg: rgba(239, 68, 68, 0.1);
    --color-error-border: rgba(239, 68, 68, 0.2);
}