/* ConversationPanel Styles */

.conversationPanel {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: var(--background-primary, #ffffff);
  border: 1px solid var(--border-color, #e0e0e0);
  border-radius: 8px;
  overflow: hidden;
}

/* Ensure the conversation thread takes up available space */
.conversationPanel > *:first-child {
  flex: 1;
  min-height: 0;
}

/* Keep the input panel at the bottom */
.conversationPanel > *:last-child {
  flex-shrink: 0;
}