/* Clear Data Manager Styles */

.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(2px);
}

.confirmModal {
  background: white;
  border-radius: 12px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
  max-width: 600px;
  width: 90%;
  max-height: 80vh;
  overflow: hidden;
}

.confirmHeader {
  padding: 20px 24px 16px;
  border-bottom: 1px solid #e5e7eb;
}

.confirmHeader h3 {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
  color: #dc2626;
  display: flex;
  align-items: center;
  gap: 8px;
}

.confirmBody {
  padding: 20px 24px;
  max-height: 400px;
  overflow-y: auto;
}

.confirmBody p {
  margin: 0 0 16px 0;
  color: #374151;
  line-height: 1.5;
}

.confirmBody ul {
  margin: 0 0 16px 0;
  padding-left: 20px;
  color: #6b7280;
}

.confirmBody li {
  margin-bottom: 4px;
}

.progressContainer {
  min-height: 120px;
}

.progressList {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 16px;
}

.progressItem {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 8px 12px;
  background: #f9fafb;
  border-radius: 6px;
  font-size: 14px;
}

.progressIcon {
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  flex-shrink: 0;
}

.progressIcon.pending {
  opacity: 0.6;
}

.progressIcon.running {
  animation: spin 1s linear infinite;
}

.progressIcon.success {
  color: #10b981;
}

.progressIcon.error {
  color: #ef4444;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.confirmActions {
  padding: 16px 24px 24px;
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  border-top: 1px solid #e5e7eb;
}

.cancelButton {
  padding: 8px 16px;
  border: 1px solid #d1d5db;
  background: white;
  color: #374151;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.cancelButton:hover:not(:disabled) {
  background: #f9fafb;
  border-color: #9ca3af;
}

.cancelButton:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.confirmButton {
  padding: 8px 16px;
  border: 1px solid #dc2626;
  background: #dc2626;
  color: white;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.confirmButton:hover:not(:disabled) {
  background: #b91c1c;
  border-color: #b91c1c;
}

.confirmButton:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Scope Selector Styles */
.scopeSelector {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin: 16px 0;
}

.scopeItem {
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  overflow: hidden;
  transition: all 0.2s;
}

.scopeItem:hover {
  border-color: #d1d5db;
}

.scopeItem.full {
  border-color: #3b82f6;
  background: #eff6ff;
}

.scopeItem.partial {
  border-color: #f59e0b;
  background: #fffbeb;
}

.scopeItem.unchecked {
  background: #ffffff;
}

.scopeHeader {
  padding: 0;
}

.scopeLabel {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  padding: 16px;
  cursor: pointer;
  margin: 0;
  width: 100%;
}

.scopeCheckbox {
  margin: 0;
  margin-top: 2px;
  flex-shrink: 0;
  width: 16px;
  height: 16px;
  accent-color: #3b82f6;
}

.scopeInfo {
  flex: 1;
  min-width: 0;
}

.scopeTitle {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 4px;
  font-weight: 500;
  color: #111827;
}

.scopeIcon {
  font-size: 16px;
  flex-shrink: 0;
}

.scopeName {
  flex: 1;
  font-size: 15px;
}

.taskCount {
  font-size: 13px;
  color: #6b7280;
  font-weight: 400;
}

.expandButton {
  background: none;
  border: none;
  color: #6b7280;
  cursor: pointer;
  font-size: 12px;
  padding: 2px 4px;
  border-radius: 4px;
  transition: all 0.2s;
}

.expandButton:hover {
  background: #f3f4f6;
  color: #374151;
}

.scopeDescription {
  font-size: 13px;
  color: #6b7280;
  line-height: 1.4;
}

.singleTaskInfo {
  color: #9ca3af;
  font-style: italic;
}

.taskList {
  padding: 0 16px 16px 44px; /* Align with checkbox + gap */
  display: flex;
  flex-direction: column;
  gap: 8px;
  border-top: 1px solid #f3f4f6;
  margin-top: 8px;
  padding-top: 12px;
}

.taskItem {
  display: flex;
  align-items: flex-start;
  padding: 0;
  font-size: 13px;
}

.taskLabel {
  display: flex;
  align-items: flex-start;
  gap: 8px;
  padding: 8px 12px;
  background: rgba(255, 255, 255, 0.8);
  border: 1px solid #f3f4f6;
  border-radius: 6px;
  cursor: pointer;
  margin: 0;
  width: 100%;
  transition: all 0.2s;
}

.taskLabel:hover {
  background: #f9fafb;
  border-color: #e5e7eb;
}

.taskCheckbox {
  margin: 0;
  margin-top: 1px;
  flex-shrink: 0;
  width: 14px;
  height: 14px;
  accent-color: #3b82f6;
}

.taskIcon {
  flex-shrink: 0;
  margin-top: 1px;
  opacity: 0.7;
  font-size: 14px;
}

.taskInfo {
  flex: 1;
  min-width: 0;
}

.taskName {
  font-weight: 500;
  color: #374151;
  margin-bottom: 2px;
  font-size: 13px;
}

.taskDescription {
  color: #6b7280;
  font-size: 12px;
  line-height: 1.3;
}

.warningMessage {
  margin-top: 20px;
  padding: 12px;
  background: #fef3cd;
  border: 1px solid #fcd34d;
  border-radius: 6px;
  color: #92400e;
  font-size: 14px;
  text-align: center;
}

/* Enhanced Progress Styles */
.progressHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  padding-bottom: 12px;
  border-bottom: 1px solid #e5e7eb;
}

.progressHeader h4 {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
  color: #111827;
}

.progressSummary {
  display: flex;
  align-items: center;
  gap: 12px;
}

.progressStats {
  font-size: 14px;
  font-weight: 500;
  color: #374151;
  padding: 4px 8px;
  background: #f3f4f6;
  border-radius: 12px;
}

.progressBar {
  width: 100%;
  height: 8px;
  background: #e5e7eb;
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 20px;
}

.progressBarFill {
  height: 100%;
  background: linear-gradient(90deg, #3b82f6, #10b981);
  transition: width 0.3s ease;
  border-radius: 4px;
}

.progressItem.pending {
  background: #f9fafb;
  border-left: 3px solid #9ca3af;
}

.progressItem.running {
  background: #eff6ff;
  border-left: 3px solid #3b82f6;
  animation: progressPulse 1.5s infinite;
}

.progressItem.success {
  background: #ecfdf5;
  border-left: 3px solid #10b981;
}

.progressItem.error {
  background: #fef2f2;
  border-left: 3px solid #ef4444;
}

@keyframes progressPulse {
  0%, 100% {
    background: #eff6ff;
  }
  50% {
    background: #dbeafe;
  }
}

.progressItemContent {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.progressItemLabel {
  font-weight: 500;
  color: #111827;
  font-size: 14px;
}

.progressItemStatus {
  font-size: 12px;
  color: #6b7280;
  font-style: italic;
}