.placeholder {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 40px;
  text-align: center;
  color: #6c757d;
  background: #f8f9fa;
}

.placeholder h3 {
  margin: 0 0 16px 0;
  font-size: 24px;
  color: #495057;
}

.placeholder p {
  margin: 0;
  font-size: 16px;
  opacity: 0.8;
}