.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1100;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fadeIn 0.2s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.dialog {
  background: white;
  border-radius: 12px;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  animation: slideIn 0.2s ease-out;
  z-index: 1101;
  position: relative;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: scale(0.95) translateY(-10px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 24px 0 24px;
  border-bottom: 1px solid #e5e7eb;
  margin-bottom: 24px;
}

.header h2 {
  margin: 0;
  font-size: 24px;
  font-weight: 600;
  color: #111827;
}

.closeButton {
  background: none;
  border: none;
  font-size: 24px;
  color: #6b7280;
  cursor: pointer;
  padding: 8px;
  border-radius: 6px;
  transition: all 0.2s ease;
}

.closeButton:hover {
  color: #111827;
  background: #f3f4f6;
}

.content {
  padding: 0 24px;
  flex: 1;
  overflow-y: auto;
}

.formGroup {
  margin-bottom: 20px;
}

.formGroup label {
  display: block;
  font-size: 14px;
  font-weight: 600;
  color: #374151;
  margin-bottom: 8px;
}

.labelRow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.toggleContainer {
  display: flex;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  overflow: hidden;
}

.toggleButton {
  background: white;
  border: none;
  padding: 6px 12px;
  font-size: 12px;
  font-weight: 500;
  color: #6b7280;
  cursor: pointer;
  transition: all 0.2s ease;
}

.toggleButton:hover {
  background: #f9fafb;
}

.toggleButton.active {
  background: #3b82f6;
  color: white;
}

.input {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  font-size: 14px;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.textarea {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  font-size: 14px;
  font-family: inherit;
  resize: vertical;
  min-height: 120px;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.textarea:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.markdownPreview {
  border: 1px solid #d1d5db;
  border-radius: 8px;
  padding: 12px 16px;
  min-height: 120px;
  background: #fafafa;
  font-size: 14px;
  line-height: 1.6;
  color: #374151;
}

.markdownPreview h1,
.markdownPreview h2,
.markdownPreview h3,
.markdownPreview h4,
.markdownPreview h5,
.markdownPreview h6 {
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
  line-height: 1.3;
}

.markdownPreview h1 { font-size: 1.125rem; }
.markdownPreview h2 { font-size: 1rem; }
.markdownPreview h3 { font-size: 0.9375rem; }

.markdownPreview p {
  margin-bottom: 0.75rem;
}

.markdownPreview ul,
.markdownPreview ol {
  padding-left: 1.25rem;
  margin-bottom: 0.75rem;
}

.markdownPreview li {
  margin-bottom: 0.25rem;
}

.markdownPreview code {
  background: #f3f4f6;
  color: #e11d48;
  padding: 0.125rem 0.25rem;
  border-radius: 0.25rem;
  font-size: 0.875em;
  font-family: ui-monospace, SFMono-Regular, "SF Mono", Consolas, "Liberation Mono", Menlo, monospace;
}

.markdownPreview pre {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 0.375rem;
  padding: 0.75rem;
  margin: 0.75rem 0;
  overflow-x: auto;
  font-family: ui-monospace, SFMono-Regular, "SF Mono", Consolas, "Liberation Mono", Menlo, monospace;
  font-size: 0.875rem;
}

.markdownPreview pre code {
  background: none;
  color: inherit;
  padding: 0;
  border-radius: 0;
}

.markdownPreview strong {
  font-weight: 600;
  color: #111827;
}

.markdownPreview em {
  font-style: italic;
  color: #6b7280;
}

.markdownPreview blockquote {
  border-left: 4px solid #e5e7eb;
  padding-left: 1rem;
  margin: 1rem 0;
  color: #6b7280;
  font-style: italic;
}

.emptyPreview {
  color: #9ca3af;
  font-style: italic;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px;
  border-top: 1px solid #e5e7eb;
  margin-top: 24px;
}

.deleteButton {
  background: #dc2626;
  border: 1px solid #dc2626;
  color: white;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.deleteButton:hover {
  background: #b91c1c;
  border-color: #b91c1c;
}

.actionButtons {
  display: flex;
  gap: 12px;
}

.cancelButton {
  background: white;
  border: 1px solid #d1d5db;
  color: #374151;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.cancelButton:hover {
  background: #f9fafb;
  border-color: #9ca3af;
}

.saveButton {
  background: #3b82f6;
  border: 1px solid #3b82f6;
  color: white;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.saveButton:hover:not(:disabled) {
  background: #2563eb;
  border-color: #2563eb;
}

.saveButton:disabled {
  background: #9ca3af;
  border-color: #9ca3af;
  cursor: not-allowed;
  opacity: 0.6;
}

/* Mobile responsiveness */
@media (max-width: 640px) {
  .dialog {
    width: 95%;
    margin: 20px;
  }
  
  .footer {
    flex-direction: column;
    gap: 12px;
  }
  
  .actionButtons {
    flex-direction: column-reverse;
    width: 100%;
  }
  
  .cancelButton,
  .saveButton,
  .deleteButton {
    width: 100%;
  }
}