.splitContainer {
  display: grid;
  grid-template-columns: 50% 4px 50%; /* Default, overridden by inline style */
  grid-template-rows: 1fr;
  gap: 0;
  overflow: hidden;
  height: 100%;
}

/* Mobile responsive */
@media (max-width: 768px) {
  .splitContainer {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 300px;
    gap: 1px;
  }
}

.leftPanel {
  grid-column: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border-right: 1px solid #e9ecef;
}

.rightPanel {
  grid-column: 3;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* Mobile - adjust grid positions */
@media (max-width: 768px) {
  .leftPanel {
    grid-column: 1;
    grid-row: 1;
    border-right: none;
    border-bottom: 1px solid #e9ecef;
  }
  
  .rightPanel {
    grid-column: 1;
    grid-row: 2;
  }
}

/* Resize handle */
.resizeHandle {
  grid-column: 2;
  background: #f8f9fa;
  cursor: col-resize;
  border-left: 1px solid #e9ecef;
  border-right: 1px solid #e9ecef;
  transition: background-color 0.2s ease;
  position: relative;
}

.resizeHandle:hover {
  background: #e9ecef;
}

.resizeHandle.dragging {
  background: #dee2e6;
}

/* Visual indicator on hover */
.resizeHandle:hover::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 2px;
  height: 40px;
  background: #6c757d;
  border-radius: 1px;
}

/* Hide resize handle on mobile */
@media (max-width: 768px) {
  .resizeHandle {
    display: none;
  }
}