/* Message Input Panel Styles */

.inputPanel {
  border-top: 1px solid var(--border-color, #e0e0e0);
  background: var(--background-primary, #ffffff);
  padding: 1rem 1.5rem;
}

.inputContainer {
  display: flex;
  gap: 0.75rem;
  align-items: flex-end;
  max-width: 100%;
}

.inputField {
  flex: 1;
  min-height: 44px;
  max-height: 120px;
  padding: 0.75rem 1rem;
  border: 1px solid var(--border-color, #e0e0e0);
  border-radius: 12px;
  font-size: 0.875rem;
  line-height: 1.4;
  resize: none;
  font-family: inherit;
  background: var(--input-background, #ffffff);
  color: var(--text-primary, #1a1a1a);
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.inputField:focus {
  outline: none;
  border-color: var(--primary-color, #2196F3);
  box-shadow: 0 0 0 3px rgba(33, 150, 243, 0.1);
}

.inputField:disabled {
  background: var(--background-disabled, #f5f5f5);
  color: var(--text-disabled, #999999);
  cursor: not-allowed;
}

.inputField::placeholder {
  color: var(--text-placeholder, #999999);
}

.sendButton {
  flex-shrink: 0;
  width: 44px;
  height: 44px;
  border: none;
  border-radius: 50%;
  background: var(--primary-color, #2196F3);
  color: white;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  position: relative;
}

.sendButton:hover:not(:disabled) {
  background: var(--primary-hover, #1976d2);
  transform: scale(1.05);
}

.sendButton:active:not(:disabled) {
  transform: scale(0.95);
}

.sendButton:disabled {
  background: var(--background-disabled, #f5f5f5);
  color: var(--text-disabled, #999999);
  cursor: not-allowed;
  transform: none;
}

.sendButton svg {
  width: 20px;
  height: 20px;
}

/* Loading state */
.sendButton.loading {
  pointer-events: none;
}

.sendButton.loading svg {
  animation: sendButtonSpin 1s linear infinite;
}

@keyframes sendButtonSpin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Keyboard shortcut hint */
.shortcutHint {
  font-size: 0.75rem;
  color: var(--text-tertiary, #999999);
  text-align: right;
  margin-top: 0.5rem;
  opacity: 0;
  transition: opacity 0.2s ease;
}

.inputPanel:hover .shortcutHint,
.inputField:focus + * .shortcutHint {
  opacity: 1;
}

/* Processing hint - always visible when processing */
.processingHint {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--primary-color, #2196F3);
  font-weight: 500;
}

.shortcutHint:has(.processingHint) {
  opacity: 1 !important;
}

.spinner {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .inputPanel {
    padding: 0.75rem 1rem;
  }
  
  .inputContainer {
    gap: 0.5rem;
  }
  
  .sendButton {
    width: 40px;
    height: 40px;
  }
  
  .sendButton svg {
    width: 18px;
    height: 18px;
  }
}