/* ChangeRecordInput Component Styles */

.container {
    display: flex;
    flex-direction: column;
    height: 100%;
    background: var(--color-bg-secondary, #f8f9fa);
}

.header {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
    padding: 1rem;
    background: var(--color-bg-primary, #ffffff);
    border-bottom: 1px solid var(--color-border-subtle, #e5e7eb);
}

.templateSection {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.label {
    font-size: 0.875rem;
    font-weight: 500;
    color: var(--color-text-primary, #1a1a1a);
    margin: 0;
}

.templateSelect {
    padding: 0.5rem;
    border: 1px solid var(--color-border-subtle, #e5e7eb);
    border-radius: 4px;
    background: var(--color-bg-primary, #ffffff);
    color: var(--color-text-primary, #1a1a1a);
    font-size: 0.875rem;
    cursor: pointer;
    transition: border-color 0.2s ease;
}

.templateSelect:hover {
    border-color: var(--color-border-hover, #d1d5db);
}

.templateSelect:focus {
    outline: none;
    border-color: var(--color-primary, #2563eb);
    box-shadow: 0 0 0 1px var(--color-primary, #2563eb);
}

.actions {
    display: flex;
    gap: 0.5rem;
}

.actionButton {
    padding: 0.25rem 0.75rem;
    font-size: 0.75rem;
    font-weight: 500;
    color: var(--color-text-secondary, #6b7280);
    background: var(--color-bg-secondary, #f8f9fa);
    border: 1px solid var(--color-border-subtle, #e5e7eb);
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.2s ease;
}

.actionButton:hover:not(:disabled) {
    color: var(--color-text-primary, #1a1a1a);
    background: var(--color-bg-primary, #ffffff);
    border-color: var(--color-border-hover, #d1d5db);
}

.actionButton:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

.inputSection {
    display: flex;
    flex-direction: column;
    flex: 1;
    padding: 1rem;
    gap: 0.5rem;
    min-height: 0;
}

.textarea {
    flex: 1;
    min-height: 200px;
    padding: 0.75rem;
    border: 1px solid var(--color-border-subtle, #e5e7eb);
    border-radius: 4px;
    background: var(--color-bg-primary, #ffffff);
    color: var(--color-text-primary, #1a1a1a);
    font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
    font-size: 0.875rem;
    line-height: 1.5;
    resize: none;
    transition: border-color 0.2s ease;
}

.textarea:hover {
    border-color: var(--color-border-hover, #d1d5db);
}

.textarea:focus {
    outline: none;
    border-color: var(--color-primary, #2563eb);
    box-shadow: 0 0 0 1px var(--color-primary, #2563eb);
}

.textarea::placeholder {
    color: var(--color-text-tertiary, #9ca3af);
    font-style: italic;
}

.validationStatus {
    padding: 0.5rem 1rem;
    background: var(--color-bg-primary, #ffffff);
    border-top: 1px solid var(--color-border-subtle, #e5e7eb);
    border-bottom: 1px solid var(--color-border-subtle, #e5e7eb);
}

.validating {
    font-size: 0.875rem;
    color: var(--color-primary, #2563eb);
    font-style: italic;
}

.validating::after {
    content: '';
    animation: dots 1.5s infinite;
}

@keyframes dots {
    0% { content: ''; }
    25% { content: '.'; }
    50% { content: '..'; }
    75% { content: '...'; }
}

.info {
    padding: 1rem;
    background: var(--color-bg-info, #f0f9ff);
    border-top: 1px solid var(--color-border-subtle, #e5e7eb);
}

.infoText {
    margin: 0;
    font-size: 0.875rem;
    line-height: 1.4;
    color: var(--color-text-info, #0369a1);
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
    .container {
        --color-bg-primary: #1a1a1a;
        --color-bg-secondary: #2d2d2d;
        --color-bg-info: #1e293b;
        --color-text-primary: #ffffff;
        --color-text-secondary: #cccccc;
        --color-text-tertiary: #9ca3af;
        --color-text-info: #38bdf8;
        --color-border-subtle: #404040;
        --color-border-hover: #525252;
        --color-primary: #3b82f6;
    }
}

/* Light mode fallback */
.container {
    --color-bg-primary: #ffffff;
    --color-bg-secondary: #f8f9fa;
    --color-bg-info: #f0f9ff;
    --color-text-primary: #1a1a1a;
    --color-text-secondary: #6b7280;
    --color-text-tertiary: #9ca3af;
    --color-text-info: #0369a1;
    --color-border-subtle: #e5e7eb;
    --color-border-hover: #d1d5db;
    --color-primary: #2563eb;
}