.appLayout {
  width: 98vw;
  height: 95vh;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 12px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  display: grid;
  grid-template-rows: auto 1fr;
  gap: 0;
  overflow: hidden;
}

/* Mobile responsive */
@media (max-width: 768px) {
  .appLayout {
    width: 95vw;
    height: 90vh;
    border-radius: 20px;
  }
}

.mainContent {
  overflow: hidden;
}

.rightPanelContent {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}

/* Modal styles for confirmation dialog */
.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(2px);
}

.confirmModal {
  background: white;
  border-radius: 12px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
  max-width: 480px;
  width: 90%;
  max-height: 80vh;
  overflow: hidden;
}

.confirmHeader {
  padding: 20px 24px 16px;
  border-bottom: 1px solid #e5e7eb;
}

.confirmHeader h3 {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
  color: #dc2626;
  display: flex;
  align-items: center;
  gap: 8px;
}

.confirmBody {
  padding: 20px 24px;
}

.confirmBody p {
  margin: 0 0 16px 0;
  color: #374151;
  line-height: 1.5;
}

.confirmBody ul {
  margin: 0 0 16px 0;
  padding-left: 20px;
  color: #6b7280;
}

.confirmBody li {
  margin-bottom: 4px;
}

.confirmActions {
  padding: 16px 24px 24px;
  display: flex;
  gap: 12px;
  justify-content: flex-end;
}

.cancelButton {
  padding: 8px 16px;
  border: 1px solid #d1d5db;
  background: white;
  color: #374151;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.cancelButton:hover {
  background: #f9fafb;
  border-color: #9ca3af;
}

.confirmButton {
  padding: 8px 16px;
  border: 1px solid #dc2626;
  background: #dc2626;
  color: white;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.confirmButton:hover {
  background: #b91c1c;
  border-color: #b91c1c;
}