.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(2px);
}

.modalContent {
  background: white;
  border-radius: 12px;
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  overflow: hidden;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  display: flex;
  flex-direction: column;
}

.modalHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-bottom: 1px solid #e5e7eb;
}

.modalHeader h2 {
  margin: 0;
  font-size: 20px;
  font-weight: 600;
  color: #111827;
}

.closeButton {
  background: none;
  border: none;
  font-size: 24px;
  color: #6b7280;
  cursor: pointer;
  padding: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  transition: all 0.2s;
}

.closeButton:hover {
  background: #f3f4f6;
  color: #374151;
}

.modalBody {
  flex: 1;
  overflow-y: auto;
  padding: 24px;
}

.settingGroup {
  margin-bottom: 32px;
}

.settingGroup:last-child {
  margin-bottom: 0;
}

.settingGroup h3 {
  font-size: 16px;
  font-weight: 600;
  color: #374151;
  margin: 0 0 16px 0;
}

.settingItem {
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 1px solid #f3f4f6;
}

.settingItem:last-child {
  margin-bottom: 0;
  border-bottom: none;
  padding-bottom: 0;
}

.settingItem label {
  display: block;
  font-size: 14px;
  font-weight: 500;
  color: #374151;
  margin-bottom: 8px;
}

.apiKeyContainer {
  display: flex;
  gap: 8px;
}

.apiKeyInput {
  flex: 1;
  padding: 8px 12px;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  font-size: 14px;
  font-family: 'Monaco', 'Consolas', monospace;
  transition: border-color 0.2s;
}

.apiKeyInput:focus {
  outline: none;
  border-color: #6366f1;
}

.toggleButton {
  padding: 8px 12px;
  background: #f3f4f6;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  cursor: pointer;
  font-size: 16px;
  transition: all 0.2s;
}

.toggleButton:hover {
  background: #e5e7eb;
}

.checkboxLabel {
  display: flex;
  align-items: center;
  gap: 12px;
  cursor: pointer;
  font-size: 14px;
  color: #374151;
  margin-bottom: 4px;
}

.checkboxLabel input[type="checkbox"] {
  width: 18px;
  height: 18px;
  cursor: pointer;
  accent-color: #6366f1;
  margin: 0;
}

.helpText {
  display: block;
  font-size: 12px;
  color: #6b7280;
  margin-top: 4px;
}

.warningText {
  display: block;
  font-size: 12px;
  color: #dc2626;
  margin-top: 4px;
  font-weight: 500;
}

.resetButtons {
  display: flex;
  gap: 12px;
  margin-bottom: 8px;
}

.resetButton {
  flex: 1;
  padding: 10px 16px;
  background: #fee2e2;
  color: #dc2626;
  border: 1px solid #fecaca;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.resetButton:hover {
  background: #fecaca;
  border-color: #fca5a5;
}

.modalFooter {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  padding: 16px 24px;
  border-top: 1px solid #e5e7eb;
  background: #f9fafb;
}

.cancelButton {
  padding: 8px 16px;
  background: white;
  color: #374151;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.cancelButton:hover {
  background: #f3f4f6;
}

.saveButton {
  padding: 8px 16px;
  background: #6366f1;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.saveButton:hover {
  background: #4f46e5;
}

.saveButton:disabled,
.cancelButton:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.saveButton:disabled:hover {
  background: #6366f1;
}

.cancelButton:disabled:hover {
  background: white;
}

.spinner {
  display: inline-block;
  width: 14px;
  height: 14px;
  border: 2px solid transparent;
  border-top: 2px solid currentColor;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-right: 8px;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.disabledText {
  color: #9ca3af;
  font-style: italic;
}

.storageModeSummary {
  margin-top: 12px;
  padding: 8px 12px;
  background: #f8fafc;
  border: 1px solid #e2e8f0;
  border-radius: 6px;
}

.storageModeSummary .helpText {
  margin: 0;
  color: #475569;
}

.secureStorageWarning {
  padding: 12px;
  background: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 6px;
  margin-bottom: 16px;
}

.errorText {
  font-size: 14px;
  color: #dc2626;
  font-weight: 500;
  margin: 0 0 4px 0;
}

.secureStorageInfo {
  padding: 8px 12px;
  background: #f0fdf4;
  border: 1px solid #bbf7d0;
  border-radius: 6px;
}

.secureStorageEnabled {
  font-size: 14px;
  color: #15803d;
  font-weight: 500;
  display: block;
  margin-bottom: 4px;
}

/* New styles for dynamic settings */
.textInput {
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  font-size: 14px;
  transition: border-color 0.2s;
}

.textInput:focus {
  outline: none;
  border-color: #6366f1;
}

.securityStatus {
  margin-top: 16px;
}

.settingGroup h3 {
  font-size: 16px;
  font-weight: 600;
  color: #374151;
  margin: 0 0 16px 0;
  display: flex;
  align-items: center;
  gap: 8px;
}

/* Enhanced setting item layout */
.settingItem {
  margin-bottom: 20px;
  padding: 12px;
  border-radius: 8px;
  transition: background-color 0.2s;
}

.settingItem:hover {
  background-color: #f8fafc;
}

/* Better visual hierarchy */
.settingGroup {
  margin-bottom: 32px;
  padding: 20px;
  background: #ffffff;
  border-radius: 12px;
  border: 1px solid #e5e7eb;
}

.modalBody {
  flex: 1;
  overflow-y: auto;
  padding: 24px;
  background: #f8fafc;
}

/* Error display */
.errorDisplay {
  margin-bottom: 20px;
  padding: 12px;
  background: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 6px;
}