/* Conversation Thread Styles */

.conversationThread {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: var(--background-primary, #ffffff);
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 1.5rem;
  border-bottom: 1px solid var(--border-color, #e0e0e0);
  background: var(--background-secondary, #f8f9fa);
}

.headerContent {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  gap: 1rem;
}

.headerContent h2 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-primary, #1a1a1a);
  cursor: default;
}

.clearButton {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: transparent;
  border: 1px solid var(--border-color, #e0e0e0);
  border-radius: 6px;
  color: var(--text-secondary, #666666);
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 0.875rem;
}

.clearButton:hover:not(:disabled) {
  background: var(--background-hover, #f0f0f0);
  border-color: var(--border-hover, #d0d0d0);
}

.clearButton:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.messageList {
  flex: 1;
  padding: 1rem;
  overflow-y: auto;
  scroll-behavior: smooth;
}

.messageList::-webkit-scrollbar {
  width: 6px;
}

.messageList::-webkit-scrollbar-track {
  background: transparent;
}

.messageList::-webkit-scrollbar-thumb {
  background: var(--scrollbar-thumb, #d0d0d0);
  border-radius: 3px;
}

.messageList::-webkit-scrollbar-thumb:hover {
  background: var(--scrollbar-thumb-hover, #b0b0b0);
}

.scrollEnd {
  height: 1px;
  width: 100%;
}

/* Old processing indicator styles removed - now using SpeechBubbleTyping component */

/* Message Styles */
.message {
  display: flex;
  gap: 0.75rem;
  margin-bottom: 1.5rem;
  animation: messageSlideIn 0.2s ease-out;
}

.message:last-child {
  margin-bottom: 0;
}

.messageAvatar {
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.875rem;
  font-weight: 600;
  color: white;
}

.userMessage .messageAvatar {
  background: var(--user-color, #4CAF50);
}

.agentMessage .messageAvatar {
  background: var(--agent-color, #2196F3);
}

/* Clickable message styles */
.clickableMessage {
  cursor: pointer;
  transition: transform 0.2s ease;
}

.clickableMessage:hover .messageText {
  background: var(--message-hover-background, #f0f1f3);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
}

.errorMessage .messageAvatar {
  background: var(--error-color, #f44336);
}

.messageContent {
  flex: 1;
  min-width: 0;
}

.messageText {
  background: var(--message-background, #f8f9fa);
  border-radius: 12px;
  padding: 0.75rem 1rem;
  word-wrap: break-word;
  line-height: 1.5;
}

.userMessage .messageText {
  background: var(--user-message-bg, #e3f2fd);
  margin-left: auto;
  max-width: 80%;
}

.agentMessage .messageText {
  background: var(--agent-message-bg, #f8f9fa);
  border: 1px solid var(--border-color, #e0e0e0);
}

.errorMessage .messageText {
  background: var(--error-message-bg, #ffebee);
  border: 1px solid var(--error-border, #ffcdd2);
  color: var(--error-text, #c62828);
}

.messageFooter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 0.5rem;
  gap: 1rem;
}

.messageTimestamp {
  font-size: 0.75rem;
  color: var(--text-tertiary, #999999);
}

.messageMetadata {
  display: flex;
  gap: 0.5rem;
}

.metadataItem {
  font-size: 0.75rem;
  color: var(--text-tertiary, #999999);
  background: var(--background-tertiary, #f0f0f0);
  padding: 0.125rem 0.375rem;
  border-radius: 4px;
}

/* JSON button styles */
.jsonButton {
  padding: 0.25rem 0.5rem;
  background: rgba(99, 102, 241, 0.1);
  border: 1px solid rgba(99, 102, 241, 0.2);
  border-radius: 4px;
  color: #6366f1;
  font-size: 0.75rem;
  font-family: monospace;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  margin-left: 0.5rem;
}

.jsonButton:hover {
  background: rgba(99, 102, 241, 0.2);
  border-color: rgba(99, 102, 241, 0.4);
  transform: translateY(-1px);
}

.retryButton {
  padding: 0.375rem 0.75rem;
  background: var(--primary-color, #2196F3);
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 0.75rem;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.retryButton:hover {
  background: var(--primary-hover, #1976d2);
}

/* Markdown content styles */
.messageHeading {
  margin: 0.5em 0 0.25em 0;
  color: var(--text-primary, #1a1a1a);
}

.codeBlock {
  background: #282c34;
  color: #abb2bf;
  padding: 1rem;
  border-radius: 6px;
  overflow-x: auto;
  margin: 0.5em 0;
  font-size: 0.875rem;
  line-height: 1.4;
}

.codeBlock code {
  background: none;
  padding: 0;
  color: inherit;
}

.inlineCode {
  background: var(--code-background, #f1f3f4);
  color: var(--code-text, #c7254e);
  padding: 0.125rem 0.25rem;
  border-radius: 3px;
  font-size: 0.875rem;
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
}

.blockquote {
  border-left: 4px solid var(--primary-color, #2196F3);
  margin: 0.5em 0;
  padding-left: 1rem;
  color: var(--text-secondary, #666666);
  font-style: italic;
}

.tableWrapper {
  overflow-x: auto;
  margin: 0.5em 0;
}

.messageTable {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.875rem;
}

.messageTable th,
.messageTable td {
  border: 1px solid var(--border-color, #e0e0e0);
  padding: 0.5rem;
  text-align: left;
}

.messageTable th {
  background: var(--background-secondary, #f8f9fa);
  font-weight: 600;
}

/* Error Message Accordion Styles */
.errorSummary {
  margin-bottom: 0.75rem;
}

.errorSummary strong {
  color: var(--error-text, #c62828);
  font-size: 0.925rem;
}

.errorSummary p {
  margin: 0.25rem 0 0 0;
  color: var(--text-primary, #1a1a1a);
  font-size: 0.875rem;
}

.errorDetailsToggle {
  background: none;
  border: none;
  color: var(--primary-color, #2196F3);
  cursor: pointer;
  font-size: 0.8rem;
  padding: 0.25rem 0;
  text-align: left;
  transition: color 0.2s ease;
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.errorDetailsToggle:hover {
  color: var(--primary-hover, #1976d2);
  text-decoration: underline;
}

.errorDetails {
  margin-top: 0.75rem;
  padding: 0.75rem;
  background: var(--background-tertiary, #f5f5f5);
  border: 1px solid var(--border-color, #e0e0e0);
  border-radius: 6px;
  font-size: 0.8rem;
  animation: accordionSlideDown 0.2s ease-out;
}

.errorField {
  margin-bottom: 0.5rem;
}

.errorField:last-child {
  margin-bottom: 0;
}

.errorField strong {
  color: var(--text-secondary, #666666);
  display: block;
  margin-bottom: 0.25rem;
}

.originalMessage {
  background: var(--background-primary, #ffffff);
  border: 1px solid var(--border-color, #e0e0e0);
  border-radius: 4px;
  padding: 0.5rem;
  margin-top: 0.25rem;
  font-style: italic;
  color: var(--text-secondary, #666666);
}

@keyframes accordionSlideDown {
  from {
    opacity: 0;
    max-height: 0;
    padding-top: 0;
    padding-bottom: 0;
  }
  to {
    opacity: 1;
    max-height: 200px;
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
  }
}

@keyframes messageSlideIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}