/* SpecView Styles - Extracted from original Spec component */

.specSection {
  background: white;
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 0; /* Important for flexbox scrolling */
}

.specHeader {
  padding: 15px 20px 12px;
  border-bottom: 1px solid #f3f4f6;
  background: #fafafa;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-shrink: 0;
}

.headerLeft {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 4px;
}

.headerLeft h2 {
  font-size: 18px;
  color: #374151;
  margin: 0;
}

.headerInfo {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.templateName {
  font-size: 12px;
  color: #6b7280;
  font-weight: 500;
}

.lastUpdated {
  font-size: 11px;
  color: #059669;
  font-weight: 600;
  background-color: #ecfdf5;
  padding: 2px 6px;
  border-radius: 4px;
  border: 1px solid #a7f3d0;
}

.pendingGeneration {
  font-size: 11px;
  color: #d97706;
  font-weight: 600;
  background-color: #fef3c7;
  padding: 2px 6px;
  border-radius: 4px;
  border: 1px solid #fcd34d;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}

.headerControls {
  display: flex;
  align-items: center;
  gap: 12px;
}

.viewToggle {
  display: flex;
  background-color: #e5e7eb;
  border-radius: 6px;
  padding: 2px;
}

.toggleButton {
  background: none;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  font-weight: 500;
  color: #6b7280;
  transition: all 0.2s;
}

.toggleButton:hover {
  color: #374151;
}

.toggleButton.active {
  background-color: white;
  color: #1f2937;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}

.refreshButton {
  background: #9ca3af;
  color: white;
  border: none;
  padding: 8px;
  border-radius: 50%; /* Make it circular */
  cursor: pointer;
  font-weight: 500;
  transition: all 0.2s;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.refreshButton:hover:not(:disabled) {
  background: #6b7280;
}

.refreshButton:disabled {
  background: #d1d5db;
  cursor: not-allowed;
  opacity: 0.7;
}

.refreshButton .spinner {
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 2px solid transparent;
  border-top: 2px solid currentColor;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.editTemplatesButton {
  background: #6b7280;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  font-size: 12px;
  transition: background 0.2s;
}

.editTemplatesButton:hover {
  background: #4b5563;
}

.exportButton {
  background: #10b981;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 8px;
  font-size: 12px;
  cursor: pointer;
  transition: background 0.2s;
}

.exportButton:hover {
  background: #059669;
}

.specContent {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  font-size: 14px;
  line-height: 1.6;
  color: #374151;
}

/* Markdown styles */
.specContent .h1 {
  color: #1f2937;
  margin-bottom: 12px;
  font-size: 24px;
  border-bottom: 2px solid #e5e7eb;
  padding-bottom: 8px;
  margin-bottom: 20px;
}

.specContent .h2 {
  color: #1f2937;
  margin-bottom: 12px;
  font-size: 18px;
  margin-top: 24px;
}

.specContent .h3 {
  color: #1f2937;
  margin-bottom: 12px;
  font-size: 16px;
}

.specContent .p {
  margin-bottom: 12px;
}

.specContent .ul,
.specContent .ol {
  margin-left: 20px;
  margin-bottom: 12px;
}

.specContent .li {
  margin-bottom: 4px;
}

.specContent .table {
  width: 100%;
  border-collapse: collapse;
  margin: 16px 0;
  font-size: 13px;
}

.specContent .th,
.specContent .td {
  border: 1px solid #e5e7eb;
  padding: 8px 12px;
  text-align: left;
}

.specContent .th {
  background-color: #f9fafb;
  font-weight: 600;
  color: #374151;
}

.specContent .code {
  background-color: #f3f4f6;
  padding: 2px 4px;
  border-radius: 3px;
  font-family: 'SF Mono', 'Monaco', 'Cascadia Code', monospace;
  font-size: 12px;
  color: #dc2626;
}

.specContent .pre {
  background-color: #f9fafb;
  border: 1px solid #e5e7eb;
  border-radius: 6px;
  padding: 16px;
  margin: 16px 0;
  overflow-x: auto;
  font-family: 'SF Mono', 'Monaco', 'Cascadia Code', monospace;
  font-size: 13px;
  line-height: 1.4;
}

.specContent .blockquote {
  border-left: 4px solid #3b82f6;
  background-color: #eff6ff;
  margin: 16px 0;
  padding: 12px 16px;
  font-style: italic;
  color: #1e40af;
}

/* State styles */
.emptyState {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #6b7280;
  text-align: center;
}

.emptyStateIcon {
  font-size: 48px;
  margin-bottom: 16px;
  opacity: 0.5;
}

.emptyState h3 {
  font-size: 18px;
  color: #374151;
  margin-bottom: 8px;
}

.emptyState p {
  font-size: 14px;
}

.generatingState {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #6b7280;
  text-align: center;
}

.generatingIcon {
  font-size: 48px;
  margin-bottom: 16px;
  animation: spin 2s linear infinite;
}

.generatingState h3 {
  font-size: 18px;
  color: #374151;
  margin-bottom: 8px;
}

.generatingState p {
  font-size: 14px;
  margin-bottom: 20px;
}

.progressContainer {
  width: 100%;
  max-width: 300px;
  margin-top: 16px;
}

.progressBar {
  width: 100%;
  height: 8px;
  background-color: #e5e7eb;
  border-radius: 4px;
  overflow: hidden;
}

.progressFill {
  height: 100%;
  background-color: #3b82f6;
  transition: width 0.3s ease;
}

.progressText {
  margin-top: 8px;
  font-size: 12px;
  color: #6b7280;
}

.generatePrompt {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #6b7280;
  text-align: center;
  padding: 40px;
}

.generateIcon {
  font-size: 48px;
  margin-bottom: 16px;
}

.generatePrompt h3 {
  font-size: 18px;
  color: #374151;
  margin-bottom: 8px;
}

.generatePrompt p {
  font-size: 14px;
  margin-bottom: 20px;
}

.templateInfo {
  background-color: #f3f4f6;
  padding: 16px;
  border-radius: 8px;
  margin-bottom: 20px;
  text-align: left;
  width: 100%;
  max-width: 300px;
}

.templateInfo p {
  margin: 0;
  font-size: 13px;
  color: #374151;
  margin-bottom: 4px;
}

.templateInfo p:last-child {
  margin-bottom: 0;
}

.generateButton {
  background: #3b82f6;
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s;
}

.generateButton:hover:not(:disabled) {
  background: #2563eb;
}

.generateButton:disabled {
  background: #9ca3af;
  cursor: not-allowed;
}