/* Graph Change Debugger Styles */

.debugger {
    display: flex;
    flex-direction: column;
    height: 100%;
    background: var(--color-bg-primary);
    color: var(--color-text-primary);
}

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    border-bottom: 1px solid var(--color-border-subtle);
    background: var(--color-bg-secondary);
}

.titleSection {
    flex: 1;
}

.popOutButton {
    background: #3b82f6;
    color: white;
    border: none;
    padding: 8px 16px;
    border-radius: 6px;
    font-size: 0.875rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;
    display: flex;
    align-items: center;
    gap: 6px;
}

.popOutButton:hover {
    background: #2563eb;
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}

.title {
    margin: 0 0 0.5rem 0;
    font-size: 1.25rem;
    font-weight: 600;
    color: var(--color-text-primary);
}

.subtitle {
    margin: 0;
    font-size: 0.875rem;
    color: var(--color-text-secondary);
}

.layout {
    display: flex;
    flex: 1;
    min-height: 0;
}

.inputPanel {
    display: flex;
    flex-direction: column;
    width: 400px;
    min-width: 300px;
    max-width: 500px;
    border-right: 1px solid var(--color-border-subtle);
    background: var(--color-bg-secondary);
}

.input {
    flex: 1;
    min-height: 0;
}

.controls {
    padding: 1rem;
    border-top: 1px solid var(--color-border-subtle);
    background: var(--color-bg-primary);
}

.debugControls {
    /* Styling handled by the DebugControls component */
}

.outputPanel {
    display: flex;
    flex: 1;
    flex-direction: column;
    min-width: 0;
    background: var(--color-bg-primary);
}

.visualization {
    display: flex;
    flex-direction: column;
    flex: 1;
    min-height: 0;
}

.stepViewer {
    padding: 1rem;
    border-bottom: 1px solid var(--color-border-subtle);
    background: var(--color-bg-secondary);
}

.stepViewerComponent {
    /* Styling handled by StepByStepViewer component */
}

.graphContainer {
    flex: 1;
    min-height: 0;
    position: relative;
}

.graph {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.placeholder {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex: 1;
    padding: 2rem;
    text-align: center;
}

.emptyState h3 {
    margin: 0 0 1rem 0;
    font-size: 1.125rem;
    color: var(--color-text-primary);
}

.emptyState p {
    margin: 0 0 0.75rem 0;
    color: var(--color-text-secondary);
    line-height: 1.5;
    max-width: 400px;
}

.emptyState p:last-child {
    margin-bottom: 0;
}

.errorState h3 {
    margin: 0 0 1rem 0;
    font-size: 1.125rem;
    color: var(--color-error);
}

.errorList {
    list-style: none;
    padding: 0;
    margin: 0;
    text-align: left;
    max-width: 500px;
}

.errorItem {
    padding: 0.5rem;
    margin-bottom: 0.5rem;
    background: var(--color-error-bg, rgba(239, 68, 68, 0.1));
    color: var(--color-error);
    border-radius: 4px;
    border: 1px solid var(--color-error-border, rgba(239, 68, 68, 0.2));
    font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
    font-size: 0.875rem;
}

.errorItem:last-child {
    margin-bottom: 0;
}

/* Responsive design */
@media (max-width: 768px) {
    .layout {
        flex-direction: column;
    }
    
    .inputPanel {
        width: 100%;
        max-width: none;
        min-width: 0;
        border-right: none;
        border-bottom: 1px solid var(--color-border-subtle);
        height: 300px;
    }
    
    .outputPanel {
        min-height: 400px;
    }
}

/* Dark mode support (if using CSS variables) */
@media (prefers-color-scheme: dark) {
    .debugger {
        --color-bg-primary: #1a1a1a;
        --color-bg-secondary: #2d2d2d;
        --color-text-primary: #ffffff;
        --color-text-secondary: #cccccc;
        --color-border-subtle: #404040;
        --color-error: #ef4444;
        --color-error-bg: rgba(239, 68, 68, 0.15);
        --color-error-border: rgba(239, 68, 68, 0.3);
    }
}

/* Light mode fallback */
.debugger {
    --color-bg-primary: #ffffff;
    --color-bg-secondary: #f8f9fa;
    --color-text-primary: #1a1a1a;
    --color-text-secondary: #6b7280;
    --color-border-subtle: #e5e7eb;
    --color-error: #ef4444;
    --color-error-bg: rgba(239, 68, 68, 0.1);
    --color-error-border: rgba(239, 68, 68, 0.2);
}