.pulseRing {
  position: absolute;
  border: 2px solid var(--pulse-color, #dc2626);
  border-radius: 50%;
  animation: pulseRingAnimation var(--pulse-duration, 2s) ease-out infinite;
  pointer-events: none;
}

/* Size variants */
.small {
  top: -1px;
  left: -1px;
  right: -1px;
  bottom: -1px;
}

.medium {
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
}

.large {
  top: -4px;
  left: -4px;
  right: -4px;
  bottom: -4px;
}

@keyframes pulseRingAnimation {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  100% {
    transform: scale(1.4);
    opacity: 0;
  }
}