.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.3);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
  z-index: 999;
}

.overlayVisible {
  opacity: 1;
  visibility: visible;
}

.panel {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  width: 400px;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  box-shadow: -2px 0 8px rgba(0, 0, 0, 0.1);
  transform: translateX(100%);
  transition: transform 0.3s ease;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.panelOpen {
  transform: translateX(0);
}

.panelHeader {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
  border-bottom: 1px solid #e5e7eb;
  background: rgba(255, 255, 255, 0.8);
}

.headerContent {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.headerActions {
  display: flex;
  align-items: center;
  gap: 8px;
}

.actionButton {
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
  padding: 6px;
  border-radius: 6px;
  transition: all 0.2s ease;
  opacity: 0.7;
}

.actionButton:hover {
  background: rgba(0, 0, 0, 0.1);
  opacity: 1;
}

.panelHeader h3 {
  margin: 0;
  font-size: 20px;
  color: #111827;
  font-weight: 600;
  line-height: 1.2;
}

.headerMeta {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.nodeIdHeader {
  font-size: 12px;
  color: #6b7280;
  font-weight: 500;
}

.nodeIdHeader code {
  font-family: monospace;
  background: #f3f4f6;
  padding: 2px 6px;
  border-radius: 4px;
  font-size: 11px;
  margin-left: 4px;
}

.closeButton {
  background: none;
  border: none;
  font-size: 24px;
  color: #6b7280;
  cursor: pointer;
  padding: 4px 8px;
  line-height: 1;
  transition: color 0.2s ease;
}

.closeButton:hover {
  color: #111827;
}

.panelContent {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}

.section {
  margin-bottom: 24px;
}

.section:last-child {
  margin-bottom: 0;
}

.section label {
  display: block;
  font-size: 12px;
  font-weight: 600;
  color: #6b7280;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-bottom: 8px;
}

.section p {
  margin: 0;
  color: #374151;
  line-height: 1.6;
}

.field {
  margin-bottom: 12px;
}

.field:last-child {
  margin-bottom: 0;
}

.field label {
  margin-bottom: 4px;
}

.field span {
  color: #374151;
}

.typeTag {
  display: inline-block;
  padding: 4px 12px;
  background: #e0e7ff;
  color: #4338ca;
  border-radius: 12px;
  font-size: 14px;
  font-weight: 500;
}

.badge {
  display: inline-block;
  padding: 4px 8px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 500;
  text-transform: capitalize;
}

.badge.low {
  background: #d1fae5;
  color: #065f46;
}

.badge.medium {
  background: #fed7aa;
  color: #92400e;
}

.badge.high {
  background: #fee2e2;
  color: #991b1b;
}

.confidenceBar {
  position: relative;
  height: 24px;
  background: #f3f4f6;
  border-radius: 12px;
  overflow: hidden;
}

.confidenceFill {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  background: linear-gradient(90deg, #6366f1, #8b5cf6);
  border-radius: 12px;
  transition: width 0.3s ease;
}

.confidenceText {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 12px;
  font-weight: 600;
  color: #374151;
}

.assumptionsList {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.assumption {
  background: #f9fafb;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 12px;
}

.assumption p {
  margin: 0 0 8px 0;
  font-size: 14px;
}

.assumptionMeta {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  color: #6b7280;
}

.source {
  font-weight: 500;
}

.confidence {
  font-weight: 500;
}

.nodeId {
  display: block;
  padding: 8px 12px;
  background: #f3f4f6;
  border-radius: 6px;
  font-family: monospace;
  font-size: 12px;
  color: #6b7280;
  word-break: break-all;
}

.fieldValue {
  font-size: 14px;
  color: #374151;
  line-height: 1.5;
  padding: 4px 0;
}

/* Raw Data Section */
.rawDataDetails {
  margin-top: 8px;
}

.rawDataDetails summary {
  cursor: pointer;
  font-size: 0.9rem;
  color: #6b7280;
  padding: 4px 0;
}

.rawDataDetails summary:hover {
  color: #374151;
}

.rawDataPre {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 4px;
  padding: 12px;
  font-size: 11px;
  color: #495057;
  overflow-x: auto;
  max-height: 300px;
  overflow-y: auto;
  margin-top: 8px;
  line-height: 1.4;
}

/* Timestamp styling */
.timestampInfo {
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-size: 14px;
  color: #374151;
  line-height: 1.5;
}

.timestampInfo > div {
  display: flex;
  align-items: center;
  gap: 8px;
}

.timestampInfo strong {
  font-weight: 600;
  color: #6b7280;
  min-width: 60px;
  font-size: 13px;
}

/* Markdown Content Styling */
.markdownContent {
  line-height: 1.6;
  color: #374151;
  font-size: 0.875rem; /* 14px */
}

.mdH1 {
  font-size: 1.25rem; /* Reduced from 1.5rem */
  font-weight: 600;
  color: #111827;
  margin: 1.25rem 0 0.5rem 0; /* Reduced margins */
  line-height: 1.3;
  border-bottom: 2px solid #e5e7eb;
  padding-bottom: 0.5rem;
}

.mdH1:first-child {
  margin-top: 0;
}

.mdH2 {
  font-size: 1.125rem; /* Reduced from 1.25rem */
  font-weight: 600;
  color: #111827;
  margin: 1rem 0 0.5rem 0; /* Reduced margins */
  line-height: 1.3;
}

.mdH3 {
  font-size: 1rem; /* Reduced from 1.1rem */
  font-weight: 600;
  color: #374151;
  margin: 0.75rem 0 0.5rem 0; /* Reduced margins */
  line-height: 1.3;
}

.mdP {
  margin: 0 0 0.75rem 0; /* Reduced bottom margin */
  line-height: 1.6;
  font-size: 0.875rem; /* Ensure consistent sizing */
}

.mdUl, .mdOl {
  margin: 0.5rem 0 1rem 0;
  padding-left: 1.25rem;
}

.mdLi {
  margin: 0.25rem 0;
  line-height: 1.5;
}

.mdCode {
  background: #f3f4f6;
  color: #e11d48;
  padding: 0.125rem 0.25rem;
  border-radius: 0.25rem;
  font-family: ui-monospace, SFMono-Regular, "SF Mono", Consolas, "Liberation Mono", Menlo, monospace;
  font-size: 0.875em;
}

.mdPre {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 0.375rem;
  padding: 0.75rem;
  margin: 0.75rem 0;
  overflow-x: auto;
  font-family: ui-monospace, SFMono-Regular, "SF Mono", Consolas, "Liberation Mono", Menlo, monospace;
  font-size: 0.875rem;
  line-height: 1.5;
}

.mdPre .mdCode {
  background: none;
  color: inherit;
  padding: 0;
  border-radius: 0;
}

.mdStrong {
  font-weight: 600;
  color: #111827;
}

.mdEm {
  font-style: italic;
  color: #6b7280;
}

.mdBlockquote {
  border-left: 4px solid #e5e7eb;
  padding-left: 1rem;
  margin: 1rem 0;
  color: #6b7280;
  font-style: italic;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .panel {
    width: 100%;
    max-width: 400px;
  }
}