/* DebugDialog styles - Full-screen modal with optimized layout */

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    padding: 20px;
}

.dialog {
    background: white;
    border-radius: 12px;
    width: 95vw;
    height: 90vh;
    max-width: 1400px;
    max-height: 900px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 24px;
    border-bottom: 1px solid #e5e7eb;
    flex-shrink: 0;
}

.title {
    margin: 0;
    font-size: 1.25rem;
    font-weight: 600;
    color: #111827;
}

.closeButton {
    background: none;
    border: none;
    font-size: 24px;
    color: #6b7280;
    cursor: pointer;
    padding: 4px 8px;
    border-radius: 4px;
    transition: all 0.2s ease;
}

.closeButton:hover {
    background: #f3f4f6;
    color: #374151;
}

.content {
    flex: 1;
    display: flex;
    min-height: 0; /* Important for flex child overflow */
}

.leftPanel {
    width: 400px;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    border-right: 1px solid #e5e7eb;
    background: #f9fafb;
}

.inputSection {
    flex-shrink: 0;
    padding: 20px;
    border-bottom: 1px solid #e5e7eb;
}

.controlsSection {
    flex-shrink: 0;
    padding: 16px 20px;
    border-bottom: 1px solid #e5e7eb;
    background: white;
}

.stepsSection {
    flex: 1;
    padding: 20px;
    overflow-y: auto;
    min-height: 0;
}

.rightPanel {
    flex: 1;
    display: flex;
    flex-direction: column;
    min-height: 0;
}

.graphContainer {
    flex: 1;
    position: relative;
    min-height: 0;
}

.graph {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.placeholder {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 40px;
}

.errorState {
    text-align: center;
    max-width: 500px;
}

.errorState h3 {
    color: #dc2626;
    margin-bottom: 16px;
}

.errorList {
    text-align: left;
    background: #fef2f2;
    border: 1px solid #fecaca;
    border-radius: 8px;
    padding: 16px;
    margin: 0;
}

.errorItem {
    color: #dc2626;
    margin-bottom: 8px;
    font-size: 0.875rem;
}

.errorItem:last-child {
    margin-bottom: 0;
}

.emptyState {
    text-align: center;
    color: #6b7280;
}

.emptyState h3 {
    color: #374151;
    margin-bottom: 16px;
}

.emptyState p {
    line-height: 1.6;
    margin: 0;
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .dialog {
        width: 100vw;
        height: 100vh;
        max-width: none;
        max-height: none;
        border-radius: 0;
    }
    
    .content {
        flex-direction: column;
    }
    
    .leftPanel {
        width: 100%;
        height: 50%;
        border-right: none;
        border-bottom: 1px solid #e5e7eb;
    }
    
    .rightPanel {
        height: 50%;
    }
}